




























































import { resolveComponent } from "@vue/runtime-core";
import chardet from "chardet";
import { Result } from "element-ui";
import { Vue, Component, Watch } from "vue-property-decorator";
interface BookInfo {
  name: string;
  file: File;
}
class BookManager {
  list: BookInfo[] = [];
  cache = new Map<string, string>();

  remove(i: number) {
    this.list.splice(i, 1);
  }
  add(file: File) {
    return this.list.push({
      file,
      name: file.name,
    });
  }

  async readIndex(index: number) {
    const txt = await this.read(this.list[index].file);
    return txt;
  }

  async readWithCache(file: File) {
    const key = file.name + "_" + String(file.lastModified);
    let txt = this.cache.get(key);
    if (txt === undefined) {
      txt = await this.read(file);
      this.cache.set(key, txt);
    }
    return txt;
  }
  async read(file: File) {
    return new Promise<string>((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsArrayBuffer(file);

      reader.onload = () => {
        const result = reader.result;
        if (!(result instanceof ArrayBuffer)) {
          return;
        }

        const detBuf = result.slice(
          0,
          result.byteLength > 200 ? 200 : result.byteLength
        );
        const encode = chardet.detect(new Uint8Array(detBuf));
        if (encode === null) {
          return reject();
        }

        const decodeReader = new FileReader();
        decodeReader.readAsText(new Blob([result]), encode);
        decodeReader.onload = () => {
          if (typeof decodeReader.result !== "string") {
            return reject();
          }

          resolve(decodeReader.result);
        };
        decodeReader.onerror = (e) => {
          return reject(e);
        };
      };

      reader.onerror = (e) => {
        return reject(e);
      };
    });
  }
}

interface ChapterInfo {
  from: number;
  to: number;
  title: string;
}
class BookSource {
  chapter(chapter: ChapterInfo) {
    return this.txt.substring(chapter.from, chapter.to).split("\n");
  }
  txt: string;
  lineReg: RegExp;
  constructor(txt: string) {
    this.txt = txt;
    /* eslint-disable */
    var pp = [
      "(^\s*chapter.{0,6})|((☆、).{0,15})",
      "(^\s*第.+[章節卷回集节部篇].*$)",
      "(卷\s{0,3}[一二三四五六七八九零十百千○\d]{1,9}.*$)",
      "(第[一二三四五六七八九零十百千○\d]{1,9}[章節卷回集节部篇]$)",
      "(^[【\[\s]{0,4}(楔子)?(正文)?[】\]\s]{0,4}$)",
      "(^[0-9]([\.]?)(.*))"
    ].join("|");
    /* eslint-enable */
    this.txt = txt;
    this.lineReg = new RegExp(pp, "mgi");
  }
  chapters() {
    const chapters = [] as ChapterInfo[];
    const matcher = this.txt.matchAll(this.lineReg);
    const matches = [...matcher];

    if (matches.length === 0)
      chapters.push({
        from: 0,
        to: this.txt.length,
        title: "begin",
      });
    else if (matches.length > 0 && matches[0].index && matches[0].index !== 0)
      chapters.push({
        from: 0,
        to: matches[0].index,
        title: "begin",
      });

    for (let i = 0; i < matches.length; i++) {
      const cur = matches[i];
      const from = cur.index;
      let to: number | undefined = this.txt.length;

      if (i !== matches.length - 1) {
        const next = matches[i + 1];
        to = next.index;
      }

      const title = cur[0];
      if (from === undefined) {
        return;
      }

      if (to === undefined) {
        return;
      }

      chapters.push({
        from,
        to,
        title,
      });
    }
    return chapters;
  }
}

interface Menu {
  title: string;
  chapterInfo: ChapterInfo;
}

@Component({
  components: {},
})
export default class Test extends Vue {
  bookManager = new BookManager();

  contentProxy = [] as string[];
  contentIndex = 0;
  menu = [] as Menu[];
  source: BookSource | null = null;
  drawer = true;
  active = "0";
  infDis = false;
  mounted() {
    this.initKeyBind();
  }
  get content() {
    return this.contentProxy.slice(0, this.contentIndex);
  }
  set content(content) {
    this.contentIndex = content.length;
    if (this.contentIndex > 200) {
      this.contentIndex = 200;
      this.infDis = false;
    }
    this.contentProxy = content;
  }

  onDrawOpen() {
    this.$nextTick(() => {
      const active = document
        .querySelectorAll(".el-menu-item.is-active")
        .item(1);
      console.log(active);

      active?.scrollIntoView({ block: "center" });
    });
  }
  onDrawClose() {
    setTimeout(() => {
      this.$nextTick(() => {
        const con: any = this.$refs.content;
        console.log(con);
        con.focus({ preventScroll: false });
      });
    }, 2000);

    const scroller: any = this.$refs?.scroller;
    this.$nextTick(() => {
      scroller.focus();
    });
  }

  onInfiLoad() {
    this.contentIndex += 200;
    if (this.contentProxy.length < this.contentIndex) {
      this.contentIndex = this.contentProxy.length;
      this.infDis = true;
    }
  }

  async onSelectFile(bookInfo: BookInfo) {
    this.source = new BookSource(
      await this.bookManager.readWithCache(bookInfo.file)
    );

    const chapters = this.source.chapters();
    if (chapters === undefined) {
      return;
    }
    this.menu = chapters.map(
      (ch) => ({ title: ch.title, chapterInfo: ch } as Menu)
    );

    this.onSelectChapter(this.menu[0].chapterInfo);
  }

  selectChapterByIndex(index: number) {
    if (index >= 0 && index < this.menu.length) {
      const chapter = this.menu[index].chapterInfo;
      this.active = String(index);
      this.onSelectChapter(chapter);
    }
  }

  onSelectChapter(chapter: ChapterInfo) {
    if (this.source === null) {
      return;
    }
    this.content = this.source.chapter(chapter);

    const scroller: any = this.$refs?.scroller;
    if (typeof scroller?.scrollTo === "function") {
      scroller.scrollTo(0, 0);
    }
  }

  onDrop(env: DragEvent) {
    console.log(env);
    const files = env.dataTransfer?.files;
    if (files === undefined) {
      return;
    }
    let index = 0;
    for (let i = 0; i < files.length; i++) {
      const file = files[i];

      index = this.bookManager.add(file);
    }
    this.onSelectFile(this.bookManager.list[index - 1]);
  }
  initKeyBind() {
    window.onkeydown = (env) => {
      if (
        (env.code === "Space" && env.shiftKey && env.ctrlKey) ||
        env.code === "ArrowLeft" ||
        env.code === "KeyA" ||
        env.code === "Numpad4"
      ) {
        this.selectChapterByIndex(Number.parseInt(this.active) - 1);
      } else if (
        (env.code === "Space" && env.ctrlKey) ||
        env.code === "ArrowRight" ||
        env.code === "KeyD" ||
        env.code === "Numpad6"
      ) {
        this.selectChapterByIndex(Number.parseInt(this.active) + 1);
      } else if (env.code === "KeyT") {
        this.drawer = !this.drawer;
      }
    };
  }
}
